.checkbox {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;

    input {
        visibility: hidden;
        width: 0;
        height: auto;
    }

    label {
        font-family: $ptsans;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        padding-left: 30px;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            display: inline-block;
            width: 20px;
            height: 20px;
            background-color: $green;
            border-radius: 4px;
            left: 0;
            top: 0;
        }
    }

    input:checked + label {
        &::after {
            content: '';
            position: absolute;
            display: inline-block;
            width: 10px;
            height: 7px;
            background-size: auto;
            background-position: center;
            background-repeat: no-repeat;
            background-image: url("data:image/svg+xml,%3Csvg width='12' height='8' viewBox='0 0 12 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M11.0251 0.64131C11.2692 0.885388 11.2692 1.28112 11.0251 1.52519L5.19178 7.35853C4.9477 7.6026 4.55197 7.6026 4.3079 7.35853L0.974562 4.02519C0.730484 3.78112 0.730484 3.38539 0.974562 3.14131C1.21864 2.89723 1.61437 2.89723 1.85845 3.14131L4.74984 6.0327L10.1412 0.64131C10.3853 0.397233 10.781 0.397233 11.0251 0.64131Z' fill='white'/%3E%3C/svg%3E%0A");
            top: 6px;
            left: 5px;
        }
    }
}